<template lang="html">
  <div class="orderCreate">
    <el-breadcrumb separator="/" class="app-breadcrumb">
      <el-breadcrumb-item :to="{ name: 'OrderList' }">订单列表</el-breadcrumb-item>
      <el-breadcrumb-item>新建订单</el-breadcrumb-item>
    </el-breadcrumb>
    <el-form ref="form" :rules="rules" :model="formData" label-width="100px" class="app-form">
      <el-form-item label="产品名称：" prop="productName">
        <!-- bug: https://github.com/ElemeFE/element/pull/15565 >>> reset.scss -->
        <el-input v-model="formData.productName" placeholder="请输入产品名称" autocomplete="off" type="text" maxlength="10" show-word-limit></el-input>
      </el-form-item>
      <el-form-item label="用户名称：" prop="userName">
        <el-input v-model="formData.userName" placeholder="请输入用户名称" autocomplete="off" minlength="2" maxlength="10" show-word-limit></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('form')">立即创建</el-button>
        <el-button type="danger" @click="resetForm('form')">重置</el-button>
        <el-button @click="toOrderList">取消</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<style lang="scss" scoped>
.orderCreate {
  padding: 0 40px;

  .app {
    &-breadcrumb {
      margin-bottom: 40px;
    }

    &-form {
      width: 600px;
    }
  }
}
</style>

<script>
export default {

  name: 'orderCreate',
  data () {
    return {
      formData: {},
      rules: {
        productName: [
          { required: true, message: '请输入产品名称', trigger: 'blur' },
          {
            min: 2, max: 5, message: '长度在 2 到 10 个字符', trigger: 'blur',
          },
        ],
        userName: [
          { required: true, message: '请输入用户名称', trigger: 'blur' },
          {
            min: 2, max: 5, message: '长度在 2 到 10 个字符', trigger: 'blur',
          },
        ],
      },
    }
  },
  methods: {
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$message({
            message: '创建成功！',
            type: 'success',
          })

          return true
        }

        return false
      })
    },
    resetForm (formName) {
      console.log(formName)

      this.$refs[formName].resetFields()
    },
    toOrderList () {
      this.$router.push({
        name: 'OrderList',
      })
    },
  },

}
</script>
